body {
  color: #757575;
  background-color: #fff;
  font-family: Roboto, sans-serif;
}

a {
  text-decoration: none;
}

h1, h2, h3, p, ul {
  margin: 0;
}

ul {
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.container__hero {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.backdrop {
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal__button-close {
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__button-close:hover, .modal__button-close:focus {
  fill: #2196f3;
  cursor: pointer;
}

.modal {
  width: 100%;
  background-color: #fff;
  border-radius: 4px;
  padding: 40px;
  transition-property: background-color, transform, width, height;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 450px) {
  .modal {
    width: 450px;
    height: 609px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
    height: 581px;
  }
}

.backdrop.is-hidden .modal {
  transition: transform .5s;
  transform: scale(1.25);
}

.modal__title {
  text-align: center;
  color: #212121;
  margin-bottom: 12px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.17;
}

.modal__form {
  color: #757575;
  letter-spacing: .01em;
  font-size: 12px;
  line-height: 1.17;
}

.form__field {
  width: 100%;
  flex-direction: column;
  margin-bottom: 10px;
  display: flex;
  position: relative;
}

.form__label {
  align-items: center;
  margin-bottom: 4px;
  font-size: 12px;
  display: flex;
}

.form__icon {
  width: 18px;
  height: 18px;
  transition-property: fill;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 29px;
  left: 12px;
}

.form__input:focus + .form__icon {
  fill: #2196f3;
}

.form__input {
  height: 40px;
  box-sizing: border-box;
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  outline: none;
  padding-left: 42px;
  transition-property: border-color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.form__input:focus {
  border-color: #2196f3;
}

.form__field-comments {
  width: 100%;
  flex-direction: column;
  margin-bottom: 20px;
  display: flex;
  position: relative;
}

.form__label + .form__comments {
  resize: none;
  height: 120px;
  box-sizing: border-box;
  letter-spacing: .01em;
  color: rgba(117, 117, 117, .5);
  border: 1px solid rgba(33, 33, 33, .2);
  border-radius: 4px;
  padding: 12px 16px;
  font-size: 14px;
  line-height: 1.14;
  transition-property: border;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.form__comments:focus {
  border: 1px solid #2196f3;
  outline: none;
}

.form__label + .form__comments:not(:placeholder-shown) {
  color: #000;
}

.form__label {
  margin-bottom: 4px;
  display: flex;
  position: relative;
}

.checkbox__form {
  justify-content: center;
  margin-bottom: 30px;
  display: flex;
}

.checkbox__label {
  align-items: center;
  display: flex;
}

.checkbox__input {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.checkbox__icon {
  width: 16px;
  height: 15px;
  fill: rgba(0, 0, 0, 0);
  background-size: contain;
  background-origin: border-box;
  border: 2px solid #000;
  border-radius: 2px;
  margin-right: 7px;
  transition-property: fill, background, border;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.checkbox__input:checked ~ .checkbox__icon {
  fill: #fff;
  background: #2196f3;
  border: 2px solid #2196f3;
  border-radius: 2px;
}

.checkbox__text {
  letter-spacing: .03em;
  margin-right: 4px;
  font-size: 12px;
  line-height: 1.17;
  display: inline;
}

@media screen and (min-width: 1200px) {
  .checkbox__text {
    font-size: 14px;
    line-height: 1.71;
  }
}

.checkbox__link {
  letter-spacing: .03em;
  color: #2196f3;
  font-size: 12px;
  line-height: 1.17;
  position: relative;
}

@media screen and (min-width: 1200px) {
  .checkbox__link {
    font-size: 14px;
    line-height: 1.71;
  }
}

@media screen and (max-width: 449.8px) {
  .checkbox__link {
    -webkit-text-decoration-line: underline;
    text-decoration-line: underline;
  }
}

@media screen and (min-width: 450px) {
  .checkbox__link:after {
    content: "";
    width: 100%;
    height: 1px;
    background-color: #2196f3;
    display: block;
    position: absolute;
    bottom: 2px;
  }
}

@media screen and (min-width: 450px) and (min-width: 1200px) {
  .checkbox__link {
    bottom: 5px;
  }
}

